import React from 'react';
import { TitleCustom, OrderItem, Breadcrumb } from 'src/components/base';
import { Container, Grid } from '@material-ui/core';
import './OrderHome.scss';
import { useHistory } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { clearListSelectedHandle } from 'src/redux/actions/order.action';

const OrderHomeView = (props) => {
  const history = useHistory();
  const dispatch = useDispatch();
  const { branches } = props;
  return (
    <div className="order-home-wrapper">
      <Breadcrumb items={[{ name: 'Gọi món', url: '/order' }]} />

      {/* <Grid container lg={12} md={12} justifyContent="center">
        <Slider onClickSlider={() => {}} data={props.listBanner} />
      </Grid> */}
      <Grid container>
        <Container maxWidth="lg" className="order-home-container">
          <div style={{ marginTop: '50px', marginBottom: '30px' }}>
            <TitleCustom title="Gọi Món Tại Nhà Hàng" />
          </div>

          <Grid container>
            {branches.map((el) => (
              <OrderItem
                onClick={() => {
                  dispatch(clearListSelectedHandle());
                  history.push(`/order/${el.id}`, { branch: el });
                }}
                item={el}
              />
            ))}
          </Grid>
        </Container>
      </Grid>
    </div>
  );
};

export default React.memo(OrderHomeView);
