import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getListBranchByIdHandle } from 'src/redux/actions/branch.action';
import { getListBannerSelector } from 'src/redux/selectors/common.selector';
import OrderHomeView from './OrderHome.view';

const OrderHomeContainer = () => {
  const dispatch = useDispatch();
  const listBanner = useSelector(getListBannerSelector);
  const [branches, setBranches] = useState([]);
  useEffect(() => {
    dispatch(
      getListBranchByIdHandle(
        1,
        100,
        {
          merchant_id: 934,
        },
        (listBranches) => {
          setBranches((prevState) => [...prevState, ...listBranches]);
        }
      )
    );
    dispatch(
      getListBranchByIdHandle(
        1,
        100,
        {
          merchant_id: 935,
        },
        (listBranches) => {
          setBranches((prevState) => [...prevState, ...listBranches]);
        }
      )
    );
  }, []);
  return <OrderHomeView listBanner={listBanner} branches={branches} />;
};

export default React.memo(OrderHomeContainer);
